#app2 {
    border: 1px solid red;
}
.tab-bar {
    display: flex;
}
.tab-bar > li {
    border: 1px solid black;
    width: 50%;
}
.tab-bar > li.selected {
    background: rgb(14, 75, 154);
    color: white;
}
.tab-content > li {
    display: none;
}
.tab-content > li.show {
    display: block;
}